@import '../../styles/mixins/Mixins.module';
@import '../../styles/colors/Colors.module';

.wrapper {
  @include wrapper
}

.dog-img {
  height: 220px;
  margin: 10px auto;
  border-radius: 5px;
  display: block;
  max-width: 90%;
}

.dog-element {
  display: inline-block;
  width: 30%;
  margin: 10px;
}

.add-button {
  @include button ($dogify-btn, $black, $dogify-btn, $add-btn);
}

.add-button .add-text {
  display: none;
}

.add-button:hover .add-icon {
  display: none;
}

.add-button:hover .add-text {
  display: inline;
}

.dogified .add-icon {
  display: none;
}

.dogified {
  @include button ($dogify-btn, $black);
  pointer-events: none;
}


