@mixin wrapper {
  text-align: center;
  width: 69%;
  float: right
}

@mixin button ($bg:'', $color:'', $hover-bg:'', $hover-color:'') {
  display: inline-block;

  font: {
    size: 1rem;
    weight: bold;
  }
  cursor: pointer;
  padding: 12px;
  background: $bg;
  color: $color;
  border: none;
  border-radius: 4px;
  transition: all 0.6s;
  &:focus {
    outline: 0;
  }
  &:hover {
    background: $hover-bg;
    color: $hover-color;
  }
}


