@import '../../../styles/colors/Colors.module';
@import '../../../styles/mixins/Mixins.module';

.select {
  margin: 15px 15px 0 0;
  width: 300px;
  -webkit-appearance: none;
  padding: 10px;
  background: $white;
  font-size: 1.2em;
  &:focus {
    outline: 0;
  }
}

.dogify-button {
  @include button($dogify-btn, $black, $add-btn, $dogify-btn);
}


