@import '../../styles/mixins/Mixins.module';
@import '../../styles/colors/Colors.module';

.wrapper {
  @include wrapper
}

.dog-img {
  height: 220px;
  margin: 10px auto;
  border-radius: 5px;
  display: block;
  max-width: 90%;
}

.dog-element {
  display: inline-block;
  width: 30%;
  margin: 10px;
}

.delete-button {
  @include button ($black, $dogify-btn, $dogify-btn, $black);
}

.delete-button .delete-text {
  display: none;
}

.delete-button:hover .trash-icon {
  display: none;
}

.delete-button:hover .delete-text {
  display: inline;
}


